import React, { Component } from 'react';
import './App.css';
import { createStore,combineReducers } from 'redux';

function cat(state=[],action){
	if(action.type === 'ADD_CAT'){
		state.push(action.cat);
		return state;
	}else{
		return state;
	}
}
function dog(state=[],action){
	if(action.type === 'ADD_DOG'){
		state.push(action.dog);
		return state;
	}else{
		return state;
	}
}


const reducer = combineReducers({dog,cat});
const store = createStore(reducer);

class App extends Component {
	componentDidMount(){
		const _this = this;
		 store.subscribe(function(){
		 	_this.setState({});
		 });
	}
	
	calculator(ev){
		console.log(reducer);
		const index=ev.target.innerText;
		console.log(ev.target.innerText)
		if(index==='+'){
			store.dispatch({type: 'ADD_CAT',cat:'tom'})
		}else if(index=='-'){
			store.dispatch({type: 'ADD_DOG',dog:'jerry'})
		}
	}
  render() {
    return (
      <div className="App">
      	<h1>{store.getState().dog}</h1>
      	<h1>{store.getState().cat}</h1>
			  <button onClick={this.calculator}>+</button>
			  <button onClick={this.calculator}>-</button>
      </div>
    );
  };
}


export default App;



